import {
  trigger,
  animate,
  transition,
  style,
  query
} from '@angular/animations';

export const fadeFwdAnimation = trigger('fadeFwdAnimation', [
  // The '* => *' will trigger the animation to change between any two states
  transition('* => *', [
    animate('.3s ease-in', style({
      opacity: 1,
      transform: 'translateX(-100%)'
    })),
    animate('1ms ease-in', style({
      opacity: 0,
      transform: 'translateX(100%)'
    })),
    animate('.3s ease-in-out', style({
      opacity: 1,
      transform: 'translateX(0%)'
    }))  
  ]
)
]);

export const fadeBwdAnimation = trigger('fadeBwdAnimation', [
  // The '* => *' will trigger the animation to change between any two states
  transition('* => *', [
    animate('.3s ease-in', style({
      opacity: 1,
      transform: 'translateX(100%)'
    })),
    animate('1ms ease-in', style({
      opacity: 0,
      transform: 'translateX(-100%)'
    })),
    animate('.3s ease-in-out', style({
      opacity: 1,
      transform: 'translateX(0%)'
    }))  
  ]
)
]);
